import React from 'react';
import { prepareProps } from "../utils/controlUtils";

export const CustomInput = React.memo(
  (props: any) => {
    let {
      name="",
      type="",
      label="",
      value="",
      clsName="",
      fieldId="",
      hidden=false,
      checked=false,
      disabled=false,
      placeholder="",
      inputClsName="",
      hideLabel=false,
      prependLabel=true,
      onChange=() => null,
      onClick=() => null,
    } = prepareProps(props);

    const handleOnChange = (evt: any) => {
      let _id = evt.target.id;
      let value = ["checkbox", "radio"].includes(evt.target.type)
        ? evt.target.checked
        : evt.target.value;

      value = evt.target.type === "number"
        ? parseInt(value)
        : value

      onChange(_id, value);
    }

    if(hidden)
      clsName = `${clsName} hidden`;

    return (
      <div className={clsName}>
        {!hideLabel && prependLabel && <label className="control-label">{label}</label>}
        <input
          type={type}
          id={fieldId}
          name={name}
          value={value}
          checked={checked}
          disabled={disabled}
          className={inputClsName}
          placeholder={placeholder}
          onChange={handleOnChange}
          onClick={onClick}
        />
        {!hideLabel && !prependLabel && <label className="control-label">{label}</label>}
      </div>
    )
  }
)