import React, { useState, useLayoutEffect } from 'react';

import { CustomInput } from "./components/input";
import { shouldDisableButton, getInitValues } from "./utils/controlUtils";

const PersonalDetails = (props: any) => {
  const { setStep } = props;
  const onChange = (fieldId: string, value: string) => {
    setState({ ...state, [fieldId]: value })
  }

  const onClick = () => {
    Object.keys(state).forEach((fieldId: string) => {
      localStorage.setItem(fieldId, state[fieldId]);
    })
    setStep(1);
  }

  const isDisabled = () => shouldDisableButton(FIELDS, state);
  const FIELDS = [
    { fieldId: "full_name", onChange, reqd: true },
    { fieldId: "birth_place", onChange, reqd: true },
    { type: "date", fieldId: "date_of_birth", onChange, reqd: true },
    { type: "button", fieldId: "Save", onClick, disabled: isDisabled },
  ]

  const [state, setState] = useState<any>(() => getInitValues(FIELDS));
  useLayoutEffect(() => {
    const shouldProceedNext = !shouldDisableButton(FIELDS, state);
    shouldProceedNext && setStep(1);
  }, [])

  return (
    <div className="personal-details">
    {
      FIELDS.map((field: any, idx: number) => {
        let value = field.fieldId in state? state[field.fieldId]: "";
        return <CustomInput
          key={idx}
          {...field}
          value={value}
        />
      })
    }
    </div>
  )
}

export default PersonalDetails;