
import { dashCaseToTitleCase } from '../utils';

const prepareProps = (props: any) => {
	let {
    fieldId,
    name="",
    label="",
    value="",
    clsName="",
    checked=false,
    inputClsName="",
    disabled=false,
    hideLabel=false,
    prependLabel=true
  } = props;
  label = label || dashCaseToTitleCase(fieldId);
  let placeholder = `Enter the ${label}`;
  inputClsName = `control ${inputClsName}`.trim();
  if(["radio", "checkbox"].includes(props?.type)) {
    clsName =  !clsName ? `${props?.type}-control-group`: clsName;
    prependLabel = false;
    checked = Boolean(value);
  } else if(props?.type === "button") {
    hideLabel = true;
    value=label;
  }

  clsName = !clsName? "control-group": clsName;
  disabled = typeof(disabled) === "function"
    ? disabled()
    : disabled

	return {
    ...props,
    label,
    value,
    checked,
    clsName,
    disabled,
    hideLabel,
    placeholder,
    inputClsName,
    prependLabel,
    name: name || fieldId
  };
}

const shouldDisableButton = (fields: any[], state: any) => {
  return !fields
    .filter(({ reqd=false }) => reqd)
    .map(({ fieldId }) => (
      fieldId in state && state[fieldId].trim().length
    ))
    .every((disabled: boolean) => disabled);
}

const getInitValues = (fields: any[]) => {
  return Object.assign({},
    ...fields
      .filter(({ fieldId }) => localStorage.getItem(fieldId))
      .map(({ fieldId, type }) => {
        let value: any = localStorage.getItem(fieldId);
        let parser = type === "checkbox"
          ? (value: any) => Boolean(value === "true")
          : type === "number"
          ? parseInt
          : (value: any) => value

        return { [fieldId]: parser(value) }
      })
    );
}

export {
  prepareProps,
  getInitValues,
  shouldDisableButton,
}